	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Jackknifed","date":"04 Jul 2005","slug":"/2005-07-04-jackknifed/","tags":"Y2005"},"html":"<p>\"An auto-transporter has <a href=\"http://www.urbandictionary.com/define.php?term=jackknifed&amp;defid=101058\">jackknifed</a> in the construction area on the Autobahn A1 between Maschen and Hamburg. The A1 southbound is completely closed, northbound only 1 lane is free. Commuters to Hamburg are advised to completely avoid the A1 and take the A7...\" Days like this make me glad I no longer commute.</p>"},"next":{"fields":{"title":"No Confidence","date":"01 Jul 2005","slug":"/2005-07-01-no-confidence/"}},"previous":{"fields":{"title":"Party Platform","date":"04 Jul 2005","slug":"/2005-07-04-party-platform/"}}},{"node":{"fields":{"title":"No Confidence","date":"01 Jul 2005","slug":"/2005-07-01-no-confidence/","tags":"Y2005"},"html":"<p>I watched the <a href=\"http://news.bbc.co.uk/2/hi/europe/4639781.stm\" title=\"BBC NEWS | Europe | Germany heads for early elections\">confidence vote in the Bundestag</a> this morning, hoping to watch a historical event. I haven't watched a government slowly disintegrate since Nixon resigned, and this being Germany the unraveling of the Red/Green coalition is taking place in painfully slow motion. But the debate and the vote were short and disappointing. It was almost like business as usual, and it had the feel of a planned and staged event. Probably because it was, except for <a href=\"http://www.spiegel.de/politik/deutschland/0,1518,363215,00.html\" title=\"Historische Bundestagssitzung: Aggressiver Wahlkampfauftakt am V-Tag - Politik - SPIEGEL ONLINE - Nachrichten\">Werner Schulz's speech</a> describing the process as the farce that it was.</p>"},"next":{"fields":{"title":"Chaos Management","date":"30 Jun 2005","slug":"/2005-06-30-chaos-management/"}},"previous":{"fields":{"title":"Jackknifed","date":"04 Jul 2005","slug":"/2005-07-04-jackknifed/"}}},{"node":{"fields":{"title":"Chaos Management","date":"30 Jun 2005","slug":"/2005-06-30-chaos-management/","tags":"Y2005"},"html":"<p>This week we're painting and laying flooring upstairs or, more correctly, having it done. However, the carpenter is behind, he's only half done, and is flying off on vacation next week. Currently all rooms upstairs are impassable, except (thank God!) the bathroom.</p>\n<p>The cleaning lady also comes on Thursday. (Notice how we outsource much of our work.) With the work upstairs today, she can only do the downstairs and the bathroom.</p>\n<p>Finally, Christopher woke up today with fever and a cough, so we kept him at home with me. We're staying downstairs with Christopher's Playmobil figures, the carpenter is upstairs, and the cleaning lady is pulling weeds outside. House cleaning is postponed until next week.</p>\n<p>Normally one would go to work to escape such chaos, but since I work from home, there is no escape, even if Christopher weren't sick.</p>"},"next":{"fields":{"title":"The 10 commandments of system administration","date":"27 Jun 2005","slug":"/2005-06-27-the-10-commandments-of-system-administration/"}},"previous":{"fields":{"title":"No Confidence","date":"01 Jul 2005","slug":"/2005-07-01-no-confidence/"}}}],"pathPrefix":"","first":false,"last":false,"index":299,"pageCount":1101}}

